
.project {
    /* text-align: center; */
    vertical-align: middle;
    padding-top: 1%;
    padding-bottom: 1%;
    animation-name: moveright;
    animation-duration: 1s;
    animation-delay: 0s;
}

@keyframes moveright {
    from { transform: translateX(-400%); }
    to   { transform: translateX(0px); }
 }
 @keyframes moverighta {
    from { transform: translateX(0px); }
    to   { transform: translateX(20%); }
 }
 @keyframes moverightb {
    from { transform: translateX(20%); }
    to   { transform: translateX(0px); }
 }

.project-img {
    display: inline-block;
    width: 20%;
    height: 20%;
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 1%;
    padding-bottom: 1%;
}

.another-block {
    background-color: #353a46;
}

.project-text {
    display: inline-block;
    vertical-align: middle;
    font-size: 130%;
    text-align: left;
    padding-top: 2%;
    padding-bottom: 2%;
    width: 80%;
}
