@media (prefers-reduced-motion: no-preference) {
    .footer-react-logo {
      -webkit-animation: footer-react-logo-spin infinite 10s linear;
              animation: footer-react-logo-spin infinite 10s linear;
    }
}

@-webkit-keyframes footer-react-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}

@keyframes footer-react-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
}

.about-me {
    text-align: center;
    padding-top: 5px;
    -webkit-animation-name: movertopbotom;
            animation-name: movertopbotom;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    object-fit: contain;
}

@-webkit-keyframes movertopbotom {
    from { -webkit-transform: translateY(-400%); transform: translateY(-400%); }
    to   { -webkit-transform: translateY(0px); transform: translateY(0px); }
 }

@keyframes movertopbotom {
    from { -webkit-transform: translateY(-400%); transform: translateY(-400%); }
    to   { -webkit-transform: translateY(0px); transform: translateY(0px); }
 }

