            animation-name: moveright;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

@-webkit-keyframes moveright {
    from { -webkit-transform: translateX(-400%); transform: translateX(-400%); }
    to   { -webkit-transform: translateX(0px); transform: translateX(0px); }
 }

@keyframes moveright {
    from { -webkit-transform: translateX(-400%); transform: translateX(-400%); }
    to   { -webkit-transform: translateX(0px); transform: translateX(0px); }
 }
 @-webkit-keyframes moverighta {
    from { -webkit-transform: translateX(0px); transform: translateX(0px); }
    to   { -webkit-transform: translateX(20%); transform: translateX(20%); }
 }
 @keyframes moverighta {
    from { -webkit-transform: translateX(0px); transform: translateX(0px); }
    to   { -webkit-transform: translateX(20%); transform: translateX(20%); }
 }
 @-webkit-keyframes moverightb {
    from { -webkit-transform: translateX(20%); transform: translateX(20%); }
    to   { -webkit-transform: translateX(0px); transform: translateX(0px); }
 }
 @keyframes moverightb {
    from { -webkit-transform: translateX(20%); transform: translateX(20%); }
    to   { -webkit-transform: translateX(0px); transform: translateX(0px); }
 }

